/**
 *
 * 调用示例：
 * let formData = new FormData();
 *formData.append("id",1060);
 *
 *$http.post(url,formData,headers).then((json) => {
 * //处理 请求success
 *  	if(json.code === 0 ){
 *          //我们假设业务定义code为0时，数据正常
 *      }else{
 *           //处理自定义异常
 *          this.doException(json);
 *      }
 * },(json)=>{
 *   //处理请求fail
 *
 * })
 */
//import 'whatwg-fetch'
import { Indicator, Toast } from 'mint-ui';
var i = window.localStorage.i;
var type='';
type = window.localStorage.type;
var yz_redirect = document.location.href;
	i=2;
var baseUrl = '/addons/yun_shop/api.php?i='+i+'&type='+type+'&route=';

//var baseUrl = '';
var heads = { 'Content-Type': 'application/json'};
export default {
  //封装的 GET请求
  get(url, params, message) {
    if ((message == null) || (message == undefined)) {
      Indicator.close();
    } else {
      Indicator.open(message);

    }
    if (params) {
      let paramsArray = [];
      //encodeURIComponent
      Object.keys(params).forEach(key => paramsArray.push(key + '=' + params[key]))
      // if (url.search(/\?/) === -1) {
      //   url += '?' + paramsArray.join('&')
      // } else {
      //   url += '&' + paramsArray.join('&')
      // }
      url += '&' + paramsArray.join('&');
      url = baseUrl + url;
     
    
    }else
    {
      url = baseUrl + url;
    }
      //url = url+'&'+'yz_redirect'+'='+'http://localhost:8081/member?login'+'&'+sessid;
      //url = url+'&session_id='+window.localStorage.session;     
      
    return new Promise(function (resolve, reject) {
      fetch(url, {
        method: 'GET',
        headers: {'Content-Type': 'application/json'},
        credentials: 'include'
      })
        .then((response) => {
          if (response.ok) {
            Indicator.close();
            return response.json();
          } else {
            Indicator.close();
            reject({ status: response.status })
          }
        })
        .then((response) => {
          console.log(response);
          if(response.result == 0 && response.data.login_status != undefined && response.data.login_status ==0){
              window.location.href = response.data.login_url + '&yz_redirect='+document.location.href;
              return;
          }else{
            return response;
          }
          
        })
        .then((response) => {
          resolve(response);
        })
        .catch((err) => {
          Indicator.close();
          Toast('网络请求，无响应！');
          reject({ status: -1 });
        })
    })
  },
  // POST请求  FormData 表单数据

  post(url,formData,message) {

    if ((message == null) || (message == undefined)) {
      Indicator.close();
    } else {
      Indicator.open(message);

    }
    return new Promise(function (resolve, reject) {

      url = baseUrl + url;
      fetch(url, {
        method: 'POST',
        headers: {"Content-Type": "application/json"},
        body: JSON.stringify(formData),
        credentials: 'include'
      })
        .then((response) => {
          if (response.ok) {
            Indicator.close();
             
            return response.json();
          } else {
            Indicator.close();
            Toast(response.status);
            reject({ status: response.status })
          }
        })
        .then((response) => {
          console.log(response);
          if(response.result == 0 && response.data.login_status != undefined && response.data.login_status ==0){
               window.location.href = response.data.login_url + '&yz_redirect='+document.location.href;
               return;
          }else{
            return response;
          }

        })
        .then((response) => {
          resolve(response);
        })
        .catch((err) => {
          Indicator.close();
          Toast('网络请求，无响应！');
          reject({ status: -1 });
        })
    })
  }
}


