<template>
	<div id="foot" v-if="!hide">
		<div id="allli" v-if='1'>
			<div class="footli" v-for="(item,index) in menus" :class="{'is-selected':isShow==item.id}" @click.stop="selectTabBatItem2($event,index,item)">
				
				<div class="layers" v-if="item.subMenus.length!=0" :class="{laye:isShow==item.id}">
					<ul :style="{borderColor:params.bordercolor2}"><li v-for='child in item.subMenus'><a :href="child.url"  :style="{color:params.textcolor2,borderColor:params.bordercolor2}">{{child.title}}</a></li>
						<div class="corner" :style="{'border-top-color':params.bordercolor2}"></div>
						<div class="corner2" :style="{'border-top-color':params.bgcolor2}"></div>
					</ul>
				</div>
				
				<div class="menust" :style="{flexDirection:params.showicon==2?'column':'row',borderColor:isele==ulrgl(item.url)?params.bordercolorhigh:params.bordercolor,borderStyle:params.showborder==1?'solid':'none'}">
				<div class="bgcolors" :style="{background:(isele==ulrgl(item.url) ?params.bgcolorhigh:params.bgcolor),'opacity':params.bgalpha}"></div>
					<i class="fa" :class="item.icon" :style="{color:(isele==item.name ?params.iconcolorhigh:params.iconcolor)}" v-if="params.showicon!=0" ></i>
					<span v-show='params.showtext==1' :style="{color: (isele==item.name ?params.textcolorhigh:params.textcolor2)}">{{item.title}}</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
var	params = {"previewbg":"#ef372e","height":"49px","textcolor":"#70c10b","textcolorhigh":"#ff4949","iconcolor":"#666666","iconcolorhigh":"#ff4949","bgcolor":"#FFF","bgcolorhigh":"#FFF","bordercolor":"#010101","bordercolorhigh":"#bfbfbf","showtext":1,"showborder":"0","showicon":"2","textcolor2":"#666666","bgcolor2":"#fafafa","bordercolor2":"#1856f8","showborder2":1,"bgalpha":".5"};

var menus = [
{"id":1,"title":"首页","icon":"fa fa-home","url":"/addons/yun_shop/#/home",name:'home',"subMenus":[],"textcolor":"#70c10b","bgcolor":"#24d7e6","bordercolor":"#bfbfbf","iconcolor":"#666666"},

{"id":"menu_1489731310493","title":"分类","icon":"fa fa-th-large","url":"/addons/yun_shop/#/category",name:'category',"subMenus":[],"textcolor":"#70c10b","bgcolor":"#24d7e6","iconcolor":"#666666","bordercolor":"#bfbfbf"},

{"id":"menu_1489731319695","title":"推广","icon":"fa fa-send","url":"/addons/yun_shop/#/member/extension",name:'extension',"subMenus":[],"textcolor":"#666666","bgcolor":"#837aef","iconcolor":"#666666","bordercolor":"#bfbfbf"},

{"id":"menu_1489735163419","title":"购物车","icon":"fa fa-cart-plus","url":"/addons/yun_shop/#/cart",name:'cart',"subMenus":[],"textcolor":"#70c10b","bgcolor":"#24d7e6","iconcolor":"#666666","bordercolor":"#bfbfbf"},

{"id":"menu_1491619644306","title":"会员中心","icon":"fa fa-user","url":"/addons/yun_shop/#/member",name:'member',"subMenus":[],"textcolor":"#70c10b","bgcolor":"#24d7e6","iconcolor":"#666666","bordercolor":"#bfbfbf"}];

	import { mapState,mapMutations } from 'vuex';
	import { Popup } from 'mint-ui';
	export default {
		data() {
			return {
				thisurl:window.location.href,
				selected: '',popupVisible1:false,
				isele:'',
				menus,params
			}
		},
		computed: mapState(['isShow']),
		methods:mapMutations(['toIsShow']),
		props: ['hide'],
		watch: {
			'$route' (to, from) {
				this.isele = to.name;
				
			      // 对路由变化作出响应...
			      
			    }
		},
		mounted(){
			this.isele = this.$route.name;
		},

		methods: {
			selectTabBatItem(tab){
				this.$router.push('/'+tab.id);
			},selectTabBatItem2(e,n,item){
				if (e.currentTarget.className=='footli is-selected') {
					e.currentTarget.className='footli'
				}else{
					e.currentTarget.className='footli is-selected';
				}
				this.$store.commit('toIsShow',item.id);
				if (item.subMenus.length==0) {
					//this.$router.push({name: item.id});
					window.location.href= item.url;
				}
				
			},ulrgl(urls){
				if (urls!=undefined) {
				let aurls = urls.split('#');
				return aurls[1];				
				}else{
					return '';
				}

			}

		}
	}
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
	#foot{color: #8f9295;
	.bgcolors{position: absolute;width: 100%;left: 0;right: 0;top: 0;bottom: 0;z-index: -5;}
	#allli{
		display: flex; position: fixed;bottom: 0; width: 100%; align-items: center; box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.1);
		z-index: 9999;
	.is-selected.footli{
	.layers ul{bottom: 10px;    border-radius: 3px;}
	}
	.footli{flex: 1;font-size: 0.7rem;line-height: 1rem;
		.menust{ display: flex; align-items: center;flex-direction: row;padding: 5px 0;border: #FFF solid 1px; box-sizing: border-box; border-right:0;
		 border-bottom: 0;
			i{flex: 1;}
			span{flex: 9;}
		}
		
		.layers{position: relative;
			ul{position: absolute;bottom: -300px;left: 0;border: #FFF solid 1px;width: 100%;box-sizing: border-box;background: #FFF;
				li a{display: block;border-bottom: #B1A6A6 solid 1px;line-height: 1.5rem; color: #008000;font-size: .6rem;line-height: 1.5rem;}
				li:last-of-type a{border-bottom: #B1A6A6 solid 0px;}
				}
			}
		}
	.footli:nth-child(1) .menust{border-left: 0;}
	}
	.corner {position: absolute;bottom: -20px;left: 50%;z-index: 998;width: 0;height: 0;border-width: 10px;border-style: solid dashed dashed;border-left-color: transparent;border-right-color: transparent;border-bottom-color: transparent;font-size: 0;line-height: 0;margin-left: -10px;}
	.corner2 {position: absolute; bottom: -16px;left: 50%;z-index: 999;margin-left: -8px;width: 0;height: 0;border-width: 8px;border-style: solid dashed dashed;
    border-left-color: transparent;border-right-color: transparent;border-bottom-color: transparent;font-size: 0;line-height: 0;}
	.mint-tab-item-label{font-size: 1rem; color: #8f9295;}
	.is-selected,.is-selected .fa,.index,.index .fa{color: #f15353;}
	.fa:nth-child(1){font-size: 1.5rem  ;}
	.fa{font-size: 1.4rem;padding-bottom: 3px;color: #8f9295;}
	}
</style>


