
<template >
	<div id="app">
		<div id="aaaa"></div>
		<keep-alive>
			<router-view></router-view>
		</keep-alive>
		<c-foot :hide="foot"></c-foot>
	</div>
</template>
<script>
import cFoot from 'components/foot';
import { mapState, mapMutations } from 'vuex';
export default {

	data() {
		return {
			//记录form请求微信form请求只发送一次
			selected: false,
			foot: false,
		}
	},
	watch: {

		//监测路由变化
		$route(to, from) {
			//document.title = to.meta.title;
			this.fun.setWXTitle(to.meta.title);
			this.foot = to.meta.foot;
			return;
		}

	},
	mounted(){
		let wx = window.localStorage.isWeiXin;
		let llq = window.localStorage.myuserinfo;
		console.log('tow:::::::::',llq);
		if ( wx == false && llq ) {
			console.log('浏览器打开，并已登录！');
		}
		
	},
	created() {
		var that = this;
		if (this.$store.state.city.length == 0 || this.$store.state.province.length == 0 || this.$store.state.district.length == 0) {
			//saveAddresssInfo

			$http.get('member.member-address.address').then(function (response) {

				if (response.result == 1) {
					//console.log(JSON.stringify(response.data.city));
					that.$store.commit('saveAddresssInfo', { city: response.data.city, province: response.data.province, district: response.data.district });
				}
			}, function (response) {
				// error callback
			})

		}
		//获取地址列表
	},
	activated() {
		//this.getMemberInfo();
		//this.getMailInfo();
		console.log("jfc", "activated");
	},

	beforeCreate() {
		console.log("jfc", "beforeCreate");
	},
	beforeUpdate() {
		
		this.initShare();
		console.log("jfc", "beforeUpdate");
		console.log("app:", this.$url.getKey("id"));
	},


	methods: {
		//获取商城信息
		getMailInfo() {
			let that = this;
			$http.get('setting.setting.get-setting').then((response) => {
				if (response.result == 1) {
					this.$store.commit('setMailInfo', response.data);
				}
			});
		},

		//获取用户信息
		getMemberInfo() {
			var that = this;
			$http.get('member.member.getUserInfo', '').then(function (response) {

				if (response.result == 1) {
					that.$store.commit('savemodel', response.data);
				}

			}, function (response) {
				// error callback
			});

		},


		//初始化分享设置
		initShare() {
			console.log("initShare");
			let that = this;
			console.log(document.location.href);
			let _url = document.location.href;
			let json = { url: _url };
			$http.get('member.member.wxJsSdkConfig', json).then(function (response) {
				console.log(response)
				if (response.result == 1) {
					that.share(response.data);
				} else {

				}
			}, function (response) {
				console.log(response);
			});
		},

		//组装分享设置
		share(data) {
			let that = this;
			wx.config(data.config);
			wx.ready(function () {

				let _title = data.shop.name;
				let _link = document.location.href + "&mid=" + data.info.uid;
				let _imgUrl = data.shop.logo;
				let _desc = data.shop.name;
				wx.showOptionMenu();
				wx.onMenuShareTimeline({
					title: _title, // 分享标题
					link: _link, // 分享链接
					imgUrl: _imgUrl, // 分享图标
					success: function () {
						Toast("分享成功");
					},
					cancel: function () {
						Toast("取消分享");
					}
				});



				wx.onMenuShareAppMessage({
					title: _title, // 分享标题
					desc: _desc, // 分享描述
					link: _link, // 分享链接
					imgUrl: _imgUrl, // 分享图标
					type: 'link', // 分享类型,music、video或link，不填默认为link
					dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
					success: function () {
						Toast("分享成功");
					},
					cancel: function () {
						Toast("取消分享");
					}
				});
			});
		},

	},
	components: { cFoot },
}
</script>
